ng{\n    p{\n      font-size: 20px;\n    }\n    h2{\n      font-size: 32px;\n      letter-spacing: 2px;\n    }\n  }\n}","@mixin headingStyleP(){\r\n    font-size: 14px;\r\n    font-weight: 600;\r\n    margin-bottom: 12px;\r\n    @media (max-width: 678px){\r\n        font-size: 18px;\r\n    }\r\n}\r\n@mixin headingStyleH2(){\r\n    font-size: 28px;\r\n    letter-spacing: 1px;\r\n    color: $primaryColor;\r\n    @media (max-width: 678px){\r\n        font-size: 24px;\r\n    }\r\n}\r\n@mixin flexStyle($justify, $direction){\r\n    display: flex;\r\n    justify-content: $justify;\r\n    align-items: center;\r\n    flex-direction: $direction;\r\n}\r\n@mixin sectionStyle(){\r\n    padding: 24px ;\r\n    margin-top: 120px;\r\n    min-height: 100vh;\r\n}","header{\r\n    @include flexStyle(space-between, row);\r\n    position: sticky;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    background-color: $tersiaryColor;\r\n    padding: 12px 24px;\r\n    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);\r\n    z-index: 99;\r\n    .logo {\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: center;\r\n      gap: 16px;\r\n      span svg{\r\n        width: 24px ;\r\n      }\r\n      p{\r\n        font-size: 20px;\r\n        font-weight: 600;\r\n        letter-spacing: 1px;\r\n      }\r\n    }\r\n  \r\n    nav {\r\n        position: absolute;\r\n        width: 100%;\r\n        top: 100%;\r\n        right: 0;\r\n        padding: 24px 24px;\r\n        background-color: $tersiaryColor;\r\n        display: none;\r\n        transition: all .3s ease-in-out;\r\n        color: $secondaryColor;\r\n        z-index: 10;\r\n        box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);\r\n        ul{\r\n            @include flexStyle(center, column);\r\n            gap: 32px;\r\n    \r\n            li {\r\n            padding: 4px 12px;\r\n                a{\r\n                    display: block;\r\n                    font-size: 20px;\r\n                    line-height: 44px;\r\n                    min-width: 44px;\r\n                    min-height: 44px;\r\n                    font-weight: 500;\r\n                }\r\n            }\r\n        }\r\n    }\r\n    \r\n    .open{\r\n       display: block;;\r\n    }\r\n    button:focus{\r\n        box-shadow: none;\r\n    }\r\n    .hamburger-menu{\r\n      background-color: transparent;\r\n      box-shadow: none;\r\n      padding: 12px;\r\n      display: block;\r\n      margin: 4px 0;\r\n      cursor: pointer; \r\n      span svg{\r\n        width: 24px;\r\n        height: 24px;\r\n        fill: black;\r\n      }\r\n    }\r\n    .active {\r\n        span{\r\n            margin : 6px 0;\r\n            &:nth-child(1){\r\n                width: 100%;\r\n                transform-origin: top left;\r\n                transform: rotate(45deg);\r\n            }\r\n           &:nth-child(2){\r\n                width: 0;\r\n            }\r\n           &:nth-child(3){\r\n                width: 100%;\r\n                transform-origin: bottom left;\r\n                transform: rotate(-45deg);\r\n            }\r\n        }\r\n    }\r\n    @media (min-width: 678px) {\r\n        &{\r\n            padding: 16px 40px;\r\n        }\r\n        \r\n        nav{\r\n            position: static;\r\n            width: auto;\r\n            display: block;\r\n            background-color: transparent;\r\n            box-shadow: none;\r\n            padding: 4px;\r\n            ul{\r\n                flex-direction: row;\r\n                gap: 24px;\r\n\r\n                li a{\r\n                    font-size: 16px;\r\n                }\r\n                \r\n            }\r\n        }\r\n        button{\r\n            background-color: transparent;\r\n        }\r\n        .hamburger-menu{\r\n            background-color: transparent;\r\n            display: none;\r\n        }\r\n    }\r\n    @media (min-width: 1200px){\r\n        &{\r\n            width: 1000px;\r\n            box-shadow: none;\r\n            padding: 16px 12px;\r\n            margin: 0 auto;\r\n           \r\n        }\r\n      }\r\n  }","$primaryColor :#FF9901 ;\r\n$secondaryColor : #0C0D22 ;\r\n$tersiaryColor : #ffff;\r\n$disableColor : #414141;","#jumbotron{\r\n    @include flexStyle(center, column);\r\n    margin: 120px 0;\r\n    gap: 40px;\r\n    width: 100%;\r\n    min-height: 100vh;\r\n    text-align:center;\r\n    text-transform: capitalize;\r\n    article {\r\n      @include flexStyle(flex-start, column);\r\n\r\n      width: 100%;\r\n      order: 2;\r\n      h2{\r\n        font-size: 20px;\r\n        margin-bottom: 16px;\r\n        font-weight: 800;\r\n        line-height: 36px;\r\n        span{\r\n          color: $primaryColor;\r\n        }\r\n      }\r\n      p {\r\n        font-size: 14px;\r\n        line-height: 24px;\r\n        width: 100%;\r\n        margin: 0 auto;\r\n        color: $disableColor;\r\n      }\r\n      a{\r\n        font-size: 16px;\r\n        font-weight: 500;\r\n        color: white;\r\n        background-color: $primaryColor;\r\n        padding: 12px 24px;\r\n        margin-top: 28px;\r\n        border-radius: 4px;\r\n      }\r\n\r\n      .find_button{\r\n        min-height: 44px;\r\n      }\r\n      \r\n  \r\n    }\r\n    aside{\r\n      order: 1;\r\n      width: 50%;\r\n      position: relative;\r\n      span{\r\n        img{\r\n          width: 100%;\r\n        }\r\n        &::after{\r\n          content: \"\";\r\n          position: absolute;\r\n          top: -20px;\r\n          bottom: 20px;\r\n          right: -20px;\r\n          display: inline-block;\r\n          width: 100%;\r\n          background: $secondaryColor;\r\n          z-index: -1;\r\n        }\r\n      }\r\n     \r\n    }\r\n    \r\n    @media (max-width : 370px){\r\n      article{\r\n        h2{\r\n          font-size: 24px;\r\n        }\r\n      }\r\n    }\r\n    @media (min-width : 678px){\r\n      &{\r\n        padding: 24px 40px;\r\n        min-height: 400px;\r\n        @include flexStyle(space-between, row);\r\n        text-align: left;\r\n        article{\r\n          align-items: flex-start;\r\n          order : 1;\r\n          width: 60%;\r\n          h2{\r\n            font-size: 32px;\r\n            line-height: 48px;\r\n          }\r\n          p{\r\n            font-size: 16px;\r\n            line-height: 24px;\r\n          }\r\n        }\r\n        aside{\r\n          width: 30%;\r\n        }\r\n      }\r\n    }\r\n    @media (min-width: 1200px){\r\n      &{\r\n          margin: 120px auto;\r\n          margin-bottom: 0;\r\n          width: 1000px;\r\n          article{\r\n            width: 70%;\r\n            h2{\r\n              font-size: 28px;\r\n            }\r\n            p{\r\n              font-size: 14px;\r\n            }\r\n          }\r\n      }\r\n    }\r\n  }\r\n  ",".hero{\r\n    position: relative;\r\n    display: flex;\r\n    align-items: flex-end;\r\n    color: white;\r\n    padding: 24px;\r\n    width: 100%;\r\n    min-height: 400px;\r\n    margin: 56px 0;\r\n    background-image: url(\"/src/public/images/hero.jpg\");\r\n    background-position: center;\r\n    background-attachment: fixed;\r\n    background-size: contain;\r\n    &::before{\r\n      content: '';\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n      background-color: $secondaryColor;\r\n      opacity: 60%;\r\n  \r\n    }\r\n    h1 {\r\n      width: 100%;\r\n      font-size: 24px;\r\n      z-index: 1;\r\n      text-transform: capitalize;\r\n      letter-spacing: 2px;\r\n      span{\r\n        color : $primaryColor;\r\n      }\r\n    }\r\n\r\n    @media (max-width: 600px){\r\n      &{\r\n        background-image: url(\"/src/public/images/hero-small.jpg\");\r\n      }\r\n    }\r\n\r\n    @media (min-width: 601px){\r\n      &{\r\n        background-image: url(\"/src/public/images/hero-large.jpg\");\r\n      }\r\n    }\r\n\r\n\r\n    @media (min-width : 678px) {\r\n      &{\r\n        width: 100%;\r\n        padding: 24px 40px;\r\n        height: 400px;\r\n        h1{\r\n          width: 100%;\r\n          font-size: 40px;\r\n        }\r\n      }\r\n    }\r\n    \r\n    @media (min-width: 1200px){\r\n      &{\r\n        margin: 24px auto;\r\n        padding: 24px 40px;\r\n        width: 1000px;\r\n      }\r\n    }\r\n  }\r\n  ","#foodGallery{\r\n  margin: 120px 0;\r\n  @include sectionStyle();\r\n    \r\n    .gallery{\r\n      margin-top: 24px;\r\n      overflow: hidden;\r\n      position: relative;\r\n      max-width: 100%;\r\n      height: fit-content;\r\n      display: grid;\r\n      grid-template-columns: repeat(1, 1fr);\r\n      gap: 16px;\r\n      flex-grow: 0;\r\n      \r\n      .myGallery{\r\n        aspect-ratio: 3/4;\r\n        overflow: hidden;\r\n        transition: all .5s ease-in-out;\r\n        position: relative;\r\n         @include flexStyle(center, row);\r\n        cursor: pointer;\r\n        \r\n        picture{\r\n          width: 100%;\r\n          height: 100%;\r\n          background-color: #eeeeee;\r\n\r\n            position: absolute;\r\n            top: 0;\r\n            left: 0;\r\n            z-index: -1;;\r\n          img{\r\n            width: 100%;\r\n            height: 100%;\r\n            object-fit: cover;\r\n            object-position: center;\r\n            transition: all .5s ease-in-out;\r\n          }\r\n         }\r\n        .description{\r\n          align-self: flex-end;\r\n          width: 100%;\r\n          height: 30%;\r\n          color: $tersiaryColor;\r\n          background-color:rgba(12, 13, 34, 0.6);\r\n          padding: 12px;\r\n   \r\n          transition: all .5s ease-in-out;\r\n          h3{\r\n            font-weight: 500;\r\n            font-size: 18px;\r\n          }\r\n          p{\r\n            margin-top: 8px;\r\n            font-size: 14px;\r\n            font-weight: 300;\r\n            display: none;\r\n            transition: all .3s ease-in-out;\r\n          }\r\n         \r\n        }\r\n  